// react
import React from 'react';

// third-party
import { Helmet } from 'react-helmet-async';
import { Link } from 'react-router-dom';

// application
import PageHeader from '../shared/PageHeader';

// data stubs
import theme from '../../data/theme';

function SitePageTerms() {
    const breadcrumb = [
        { title: 'Inicio', url: '' },
        { title: 'Terminos y Condiciones', url: '' },
    ];

    return (
        <React.Fragment>
            <Helmet>
                <title>{`Terminos y Condiciones  ${theme.name}`}</title>
            </Helmet>

            <PageHeader breadcrumb={breadcrumb} />

            <div className="block">
                <div className="container">
                    <div className="document">
                        <div className="document__header">
                            <h1 className="document__title">Terminos y Condiciones</h1>
                            <div className="document__subtitle">Este acuerdo fue modificado el 24 de julio del 2022.</div>
                        </div>
                        <div className="document__content typography">
                            <p>
                                Refaccionaria Ruiz, se reserva el derecho de modificar
                                trminos, precios, condiciones y descuentos en cualquier producto,
                                as como en promociones, sin responsabilidad alguna. As mismo,
                                se reserva el derecho de modificar en cualquier momento los
                                trminos y condiciones de sus polticas comerciales.
                            </p>

                            <h2>Alta de clientes</h2>
                            <h5>Requisitos para registrarse como cliente</h5>
                            <ol>
                                <li>
                                     Registro automtico en lnea :
                                    <strong> Personas fsicas </strong>
                                     Nombre completo
                                     Domicilio actual
                                     Telfono y correo electrnico
                                </li>
                                <li>
                                    <strong> Personas morales </strong>
                                     Razn social
                                     Domicilio actual
                                     Telfono y correo electrnico
                                </li>
                            </ol>

                            <h2>Polticas de ventas</h2>

                            <p>
                                Refeccionaria Ruiz se reserva el
                                derecho de vender mercanca a aquellos usuarios
                                que no estn debidamente registrados como clientes
                                mediante el formato de alta de cliente, que en algn
                                momento hayan presentado problemas de pago o legales
                                y de cualquier otro que pudiera ser en perjuicio de
                                la empresa.
                            </p>
                            <p>
                                Todas las ofertas y listas de precio, publicadas
                                manualmente o en medios electrnicos, tienen carcter
                                indicativo, Refaccionaria Ruiz se reserva
                                el derecho de aumentar o modificar dichos precios sin
                                previo aviso.
                            </p>
                            <p>
                                La aceptacin de pedidos se realiza a reserva de posibles
                                aumentos de precio, el precio ofrecido puede mantenerse
                                solo si se encuentra amparado por una cotizacin expresa y
                                vigente emitida por Refaccionaria Ruiz, sin
                                que el plazo exceda las 18:30 horas del da siguiente
                                a partir del da de la cotizacin.
                            </p>

                            <p>
                                Para obtener informacin sobre cmo ponerse en contacto con
                                nosotros, visite nuestro
                                <Link to="/sitio/contacto"> Pgina de Contacto</Link>
                                .
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}

export default SitePageTerms;
